

* {
  cursor: default;
  -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  -webkit-tap-highlight-color: transparent;
}

*:focus {
  outline:none;
}

::-webkit-scrollbar {
  width: 0px;
  height: 0px;
}

.lds-eclipse {
  margin-top: 200px;
}

.App {
  text-align: center;
  -webkit-app-region: drag;
  overflow: hidden;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.titleBar {
  position: absolute;
  -webkit-app-region: drag;
  top: 0px;
  height: 22px;
  /* background-color: #eaeaea; */
  background-color: #181818;
  width: 100vw;
  z-index: 99;
}

.titleBar p {
  line-height: 22px;
  font-size: 13px;
  margin: 0;
  color: #eaeaea;
}

.login-btn {
  position: absolute;
  right: 10px;
  top: 3px;
  font-size: 12px!important;
  max-width: 35%;
  cursor: pointer
}
.login-btn:hover {
  color: #fff;
}

.login-form {
  position: absolute;
  top: 5px;
  right: 10px;
  width: 320px;
  /* min-height: 250px; */
  height: auto;
  background-color: #292929;
  z-index: 999;
}
.log-or-reg {
  position: relative;
  margin: auto;
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: auto;
  text-align: center;
}
.log-or-reg h2 {
  font-weight: 200;
  cursor: pointer;
}
.login-form input {
  position: relative;
  margin: auto;
  width: 230px;
  height: 35px; 
  border: none;
  margin-top: 10px;
  font-size: 16px;
  padding-left: 15px;
}
.login-form p {
  position: relative;
  float: right;
  width: auto;
  /* max-width: 50%; */
  padding-right: 40px;
  text-align: right;
  cursor: pointer;
}
.login-form p:hover {
  font-weight: 400;
  text-shadow: 0px 0px 2px #fff;
}
.login-form div h3 {
  font-weight: 200;
  margin: 15px;
  margin-bottom: 5px;
}

.user-panel {
  position: relative;
  margin: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: auto;
  text-align: center;
}
.u-info {
  padding-left: 10px;
  margin: 5px;
  text-align: left!important;
  cursor: initial!important
}
.u-info:hover {
  text-shadow: none!important;
}

.u-panel-tl {
  padding-left: 10px;
  margin: 5px;
  text-align: left!important;
  font-weight: '200';
  margin-top: 25px;
  margin-bottom: 10px;
  

}

.settings-div {
  display: flex;
  flex-direction: row;
  /* background-color: #191919; */
  height: 48px;
  margin-top: 5px;
}
.settings-div label{
  text-align: left!important;
  height: 50px;
  width: 110px;
  line-height: 39px;
  padding-left: 15px;
  padding-top: 5px;
  color: #999;
}

.settings-div input {
  height: 46px;
  top: 0;
  margin-top: 0;
  background-color: #222;
  color: #aaa;
}

.context-menu {
  position: absolute;
